<template>
  <div class="box">
    <el-container>
      <!-- 头部 -->
      <h2>{{title}}</h2>
      <el-header class="header_title" height="550px">
        <div>
          <mavon-editor
            class="md"
            v-model="value"
            :subfield="false"
            :defaultOpen="'preview'"
            :toolbarsFlag="false"
            :editable="false"
            :scrollStyle="true"
            :ishljs="true"
          />
        </div>
      </el-header>
      <el-main class="mainthins">
        <!-- 比赛内容 -->
        <el-card class="main_card">
          <div>
            <el-button type="primary" @click="goto_homework_rank_list">排行榜</el-button>
            <el-table :data="tableData" border style="width: 100%" v-if="checkUseraccess" @row-click="gotoSubmit">
              <el-table-column label="状态" width="80" align="center">
                <template slot-scope="scope">
                   <i class="el-icon-check" v-if="scope.row.ac"></i>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="题目" width="800">
              </el-table-column>
              <el-table-column prop="score" label="分值"> </el-table-column>
            </el-table>
          </div>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      value: "",
      title: "",
      raceid: {
      del_contest_id: "",
      id: ""
      },
      // 检查用户报名情况参数
      checkUserRaceFlang: "",
      // 检查比赛是否开始和用户等级
      checkUseraccess: "",
      checkAc: "",
      // 用户等级
      userlevel: ""
    };
  },
  created() {
    this.getHomeworkList();
  },
  methods: {
    async getHomeworkList() {
      const { data: res } = await this.$http.get(
        `homework/homework?id=${this.$route.query.id}`
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("获取题目列表失败！");
      }
      this.tableData = res.data.prob_list;
      this.value = res.data.information;
      this.title = res.data.title;
      if(window.localStorage.getItem("access") < 3) {
        this.checkUseraccess = true;
      } else if(Date.parse(new Date(res.data.start_time)) <　Date.parse(new Date(new Date()))){
        /* console.log(res.data.start_time)
        console.log(Date.parse(new Date(res.data.start_time)))
        console.log((new Date()).getTime())
        console.log(Date.parse(new Date(res.data.start_time)) <=　Date.parse(new Date(new Date()))); */
        this.checkUseraccess = true;
      } else {
        this.checkUseraccess = false;
      }

    },
    // 进入比赛排行榜
    goto_homework_rank_list() {
      this.$router.push({path: "/homework_rank_list",query: { id: this.$route.query.id }});
    },
    gotoSubmit(row) {
      this.$router.push({ path: "/submit", query: { id: row.id } });
    }, 
    // 获取用户等级
    getuserlevel() {
      if (window.localStorage.getItem("access")<=1) this.userlevel = 1;
    },
  },
};
</script>
<style scoped>
.box {
  padding-left: 200px;
  padding-top: 20px;
}
.header_title {
  width: 80%;
  background-color: black;
}
.md {
  height: 550px;
}
.mainthins {
  padding-top: 50px;
}
.main_card {
  width: 80%;
}
.el-table {
  margin-top: 30px;
}
</style>
